 -->
<template>
  <div id="data">
    <div style="background-color:#fff;">
      <div class="list" @click="clickSelect(0)">
        <p>贷款产品</p>
        <span v-if="proSelect.dkcp==''">请选择</span>
        <span v-else v-text="proSelect.dkcp[0]+proSelect.dkcp[1]">分享</span>
        <img src="../assets/1.png" alt="">
      </div>
      <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div>
      <div class="list" @click="clickSelect(1)">
        <p>请选择金融机构</p>
        <span v-if="proSelect.jrjg==''">请选择</span>
        <span v-else v-text="proSelect.jrjg[0]">分享</span>
        <img src="../assets/1.png" alt="">
      </div>
      <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div>
      <div class="list" @click="clickSelect(2)">
        <p>首付比例</p>
        <span v-if="proSelect.sfbl==''">请选择</span>
        <span v-else v-text="proSelect.sfbl[0]*100+'%'"></span>
        <img src="../assets/1.png" alt="">
      </div>
      <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div>
      <div class="list">
        <p>尾款比例</p>
        <span v-text="proSelect.remainingMoneyRate[0]*100+'%'">30%</span>
        <!-- <img src="../assets/1.png" alt=""> -->
      </div>
      <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div>
      <div class="list" @click="clickSelect(3)">
        <p>贷款期限</p>
        <span v-if="proSelect.dkqx==''">请选择</span>
        <span v-else v-text="proSelect.dkqx[0]+'期'"></span>
        <img src="../assets/1.png" alt="">
      </div>
      <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div>
      <div class="list">
        <p>年华费率</p>
        <span>4.00%</span>
        <!-- <img src="../assets/1.png" alt=""> -->
      </div>
      <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div>
      <div class="list">
        <p>贷款城市</p>
        <span>上海</span>
        <!-- <img src="../assets/1.png" alt=""> -->
      </div>
      <!-- <div style="width:92vw;height:1px;background-color:#f5f5f5;margin:0 auto;"></div> -->
      <!-- <router-link to="/about" style="width: 100vw;text-decoration:none;"> -->
        
      <!-- </router-link> -->
      <div class="select_back" v-show="ifSelect">
        <div style="width:100vw;height:100%" @click="selectCancel"></div>
        <div class="select">
          <div class="s_top">
            <div
              style="color:#9e9e9e;width:50vw;float:left;padding-left:4vw;"
              @click="selectCancel"
            >取消</div>
            <div
              style="color:#ba6954;width:42vw;float:left;text-align: right;padding-right:4vw;"
              @click="selectOk()"
            >确定</div>
          </div>
          <mt-picker :slots="slots" @change="onValuesChange"></mt-picker>
        </div>
      </div>
    </div>
    <div @click="count" class="button">计算</div>
  </div>
</template>
<script>
import { Toast } from 'mint-ui';
export default {
  data() {
    return {
      ifSelect: false,
      indexSelect: 0,
      preSelect: {
        dkcp: [],
        productType: [],
        productSubType: [],
        remainingMoneyRate:[0],
        jrjg: [],
        sfbl: [],
        dkqx: []
      },
      proSelect: {
        dkcp: [],
        jrjg: "",
        sfbl: "",
        dkqx: "",
        remainingMoneyRate:[0],
      },
      slots: [
        // {
        //   flex: 1,
        //   values: [
        //     "2015-01",
        //     "2015-02",
        //     "2015-03",
        //     "2015-04",
        //     "2015-05",
        //     "2015-06"
        //   ],
        //   className: "slot1",
        //   textAlign: "right"
        // },
        // {
        //   divider: true,
        //   content: "-",
        //   className: "slot2"
        // },
        // {
        //   flex: 1,
        //   values: [
        //     "2015-01",
        //     "2015-02",
        //     "2015-03",
        //     "2015-04",
        //     "2015-05",
        //     "2015-06"
        //   ],
        //   className: "slot3",
        //   textAlign: "left"
        // }
      ],
      product: [
        {
          flex: 2,
          values: [],
          className: "slot1",
          defaultIndex: 0,
          textAlign: "right"
        },
        {
          flex: 1,
          values: [],
          className: "slot2",
          textAlign: "left"
        }
      ],
      mechanism: [
        {
          flex: 1,
          values: ["中国银行", "建设银行", "苏州银行", "工商银行", "交通银行"],
          className: "slot1",
          textAlign: "center"
        }
      ],
      proportion: [
        {
          flex: 1,
          values: ["20%", "30%", "40%", "50%", "60%"],
          className: "slot1",
          textAlign: "center"
        }
      ],
      deadline: [
        {
          flex: 1,
          values: ["12期", "24期", "36期", "42期", "54期"],
          className: "slot1",
          textAlign: "center"
        }
      ],
      allData: {}
    };
  },
  methods: {
    count(){
      if(!this.proSelect.dkcp[0]){
        // $toast.show('请选择贷款产品', 3000)
        Toast({message: '请选择贷款产品',duration: 3000});
        return
      }
      if(!this.proSelect.jrjg[0]){
        // $toast.show('请选择金融机构', 3000)
        Toast({message: '请选择金融机构',duration: 3000});
        return
      }
      if(!this.proSelect.sfbl[0]){
        // $toast.show('请选择首付比例', 3000)
        Toast({message: '请选择首付比例',duration: 3000});
        return
      }
      if(!this.proSelect.dkqx[0]){
        // $toast.show('请选择贷款期限', 3000)
        Toast({message: '请选择贷款期限',duration: 3000});
        return
      }
      var remainingMoneyRate
      if(this.proSelect.remainingMoneyRate[0]){
        remainingMoneyRate = this.proSelect.remainingMoneyRate[0]
      }else{
        remainingMoneyRate = ''
      }
      let key = this.proSelect.dkcp[0]+'#'+this.proSelect.productType[0]+'#'+this.proSelect.productSubType[0]+'#'+this.proSelect.dkcp[1]+'#'+this.proSelect.jrjg[0]+'#'+this.proSelect.sfbl[0]+'#'+remainingMoneyRate+'#'+this.proSelect.dkqx[0]+'#'
      console.log(key)
      
      // let key = '分享（供APP参考）#2#1#1#平安银行#0.15##12#'
      console.log(this.allData.result.products[key])

      if(!this.allData.result.products[key]){
        Toast({message: '未找到对应数据',duration: 3000});
        return
      }

      let data = {
        downPaymentRate:this.proSelect.sfbl[0],
        vechilePrice:this.allData.result.products[key].vehicleMode,
        totalAmount:this.allData.result.products[key].totalAmount,
        bindingInsurancePrice:this.allData.result.conf.bindingInsurancePrice,
        discountAmount:this.allData.result.products[key].discountAmount,
        remainingMoneyRate:this.proSelect.remainingMoneyRate[0],
        terms:this.proSelect.dkqx[0],
        rate:this.allData.result.products[key].rate,
        discountRate:this.allData.result.products[key].discountRate,
        interestRate:this.allData.result.products[key].interestRate,
        discountInterestRate:this.allData.result.products[key].discountInterestRate,
        minimalPriceOfVehicle:this.allData.result.conf.minimalPriceOfVehicle
      },totalCustomerInterest,monthlySupply,tail = 0
console.log(data)
      let downPaymentAmountCal,loanAmountCal,remainingMoneyAmountCal,subsidyAmountRate,monthlyPaymentCalRate,totalFeeCalRateBank,totalFeeCalRateCustomer,subsidyAmountInterestRate,monthlyPaymentCalInterestRate,totalFeeCalInterestRateBank,totalFeeCalInterestRateCustomer
      if(this.allData.result.products[key].remainingMoneyAmountCal !== '/'){
        remainingMoneyAmountCal =  new Function("data", `return ${this.allData.result.products[key].remainingMoneyAmountCal}`)//尾款金额计算公式
        tail = remainingMoneyAmountCal(data)
      }
      downPaymentAmountCal=  new Function("data", `return ${this.allData.result.products[key].downPaymentAmountCal}`),//首付金额计算公式
      loanAmountCal =  new Function("data", `return ${this.allData.result.products[key].loanAmountCal}`),//贷款额度计算公式

      // subsidyAmountRate =  new Function("data", `return ${this.allData.result.products[key].subsidyAmountRate}`),//贴息金额公式（以费率结算）
      monthlyPaymentCalRate =  new Function("data", `return ${this.allData.result.products[key].monthlyPaymentCalRate}`),//月供金额计算公式（以费率结算）
      // totalFeeCalRateBank =  new Function("data", `return ${this.allData.result.products[key].totalFeeCalRateBank}`),//银行总利息计算公式（以费率结算）
      totalFeeCalRateCustomer =  new Function("data", `return ${this.allData.result.products[key].totalFeeCalRateCustomer}`),//客户总利息计算公式（以费率结算）

      // subsidyAmountInterestRate =  new Function("data", `return ${this.allData.result.products[key].subsidyAmountInterestRate}`),//贴息金额（以利率结算）
      monthlyPaymentCalInterestRate =  new Function("data", `return ${this.allData.result.products[key].monthlyPaymentCalInterestRate}`),//月供金额计算公式（以利率结算）
      // totalFeeCalInterestRateBank =  new Function("data", `return ${this.allData.result.products[key].totalFeeCalInterestRateBank}`),//银行总利息计算公式（以利率结算）
      totalFeeCalInterestRateCustomer =  new Function("data", `return ${this.allData.result.products[key].totalFeeCalInterestRateCustomer }`)//客户总利息计算公式（以利率结算）
      console.log(data)
      if(this.allData.result.products[key].maintainTag == 2){
        totalCustomerInterest = totalFeeCalRateCustomer(data)
        monthlySupply = monthlyPaymentCalRate(data)
      }else{
        totalCustomerInterest = totalFeeCalInterestRateCustomer(data)
        monthlySupply = monthlyPaymentCalInterestRate(data)
      }

      this.$router.push({ path: `/about?terms=${this.proSelect.dkqx[0]}&downPaymentRate=${this.proSelect.sfbl[0]}&remainingMoneyRate=${this.proSelect.remainingMoneyRate[0]}
      &loanAmountCal=${loanAmountCal(data)}&totalCustomerInterest=${totalCustomerInterest}&downPaymentAmountCal=${downPaymentAmountCal(data)}&monthlySupply=${monthlySupply}
      &tail=${tail}` });
    },
    selectCancel() {
      this.ifSelect = false;
    },
    onValuesChange(index, values) {
      console.log(index);
      console.log(values);
      switch (this.indexSelect) {
        case 0:
          // console.log(!(this.preSelect.dkcp[0] == values[0]))
          // alert(this.preSelect.dkcp[0])
          // alert(values[0])
          if (!(this.preSelect.dkcp[0] == values[0])) {
            for (let list in this.allData.result.display) {
              console.log(this.allData.result.display[list].productName);
              console.log(values[0]);
              console.log(
                this.allData.result.display[list].productName == values[0]
              );
              if (this.allData.result.display[list].productName == values[0]) {
                this.product[1].values = [];
                this.preSelect.productType[0] = this.allData.result.display[list].productType[0].name;
                this.preSelect.productSubType[0] = this.allData.result.display[list].productType[0].data.productSubType[0].name;
                for (let o in this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries) {
                  this.product[0].defaultIndex = list;
                  this.product[1].values.push(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].name);
                }
              }
            }
          }
          console.log(this.preSelect);
          this.preSelect.dkcp = [];
          this.preSelect.dkcp[0] = values[0];
          this.preSelect.dkcp[1] = values[1];
          break;
        case 1:
          this.preSelect.jrjg = values;
          break;
        case 2:
          this.preSelect.sfbl = values;
          break;
        case 3:
          this.preSelect.dkqx = values;
          break;
        default:
          break;
      }
    },
    selectOk() {
      let data = JSON.stringify(this.preSelect);
      this.proSelect = JSON.parse(data);
      console.log(this.proSelect);
      this.ifSelect = false;
      switch (this.indexSelect) {
        case 0:
          this.proSelect.jrjg = []
          this.proSelect.sfbl = []
          this.proSelect.dkqx = []
          break;
        case 1:
          this.proSelect.sfbl = []
          this.proSelect.dkqx = []
          break;
        case 2:
          this.proSelect.dkqx = []
          break;
        case 3:

          break;
        default:
          break;
      }
      for (let list in this.allData.result.display) {
        if (this.allData.result.display[list].productName == this.proSelect.dkcp[0]) {
          for (let o in this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries) {
            if(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].name == this.proSelect.dkcp[1]){
              console.log(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data)
              this.mechanism[0].values = []
              this.proportion[0].values = []
              this.deadline[0].values = []
              this.preSelect.remainingMoneyRate = []
              // console.log(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.remainingMoneyRate[0])
              if(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.remainingMoneyRate[0])
                this.preSelect.remainingMoneyRate.push(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.remainingMoneyRate[0])
              else
                this.preSelect.remainingMoneyRate.push(0)
              
              for(let a in this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.partner){
                this.mechanism[0].values.push(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.partner[a])
              }
              for(let b in this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.downPaymentRate){
                this.proportion[0].values.push(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.downPaymentRate[b])
              }
              for(let c in this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.terms){
                this.deadline[0].values.push(this.allData.result.display[list].productType[0].data.productSubType[0].data.productSeries[o].data.terms[c])
              }
            }
          }
        }
      }
    },
    clickSelect(i) {
      switch (i) {
        case 0:
          this.slots = this.product;
          break;
        case 1:
          if(this.proSelect.dkcp[0]){
            this.slots = this.mechanism;
          }else{
            Toast({message: '请先选择贷款产品',duration: 3000});
            return
          }
          break;
        case 2:
          if(this.proSelect.dkcp[0]){
            if(this.proSelect.jrjg[0]){
              this.slots = this.proportion;
            }else{
              Toast({message: '请先选择金融机构',duration: 3000});
              return
            }
          }else{
            Toast({message: '请先选择贷款产品',duration: 3000});
            return
          }
          
          break;
        case 3:
          if(this.proSelect.dkcp[0]){
            if(this.proSelect.jrjg[0]){
              if(this.proSelect.sfbl[0]){
                this.slots = this.deadline;
              }else{
                Toast({message: '请先选择首付比例',duration: 3000});
                return
              }
            }else{
              Toast({message: '请先选择金融机构',duration: 3000});
              return
            }
          }else{
            Toast({message: '请先选择贷款产品',duration: 3000});
            return
          }
          
          break;
        default:
          break;
      }
      this.indexSelect = i;
      this.ifSelect = true;
    }
  },
  mounted() {
      this.$http({
      url: 'https://ec-service-test.byton.cn/api-vendor/v2/portal/vendor/product/cal?cityCode=NJ&mode=&edition=',
      type: 'GET',
      data:''
    }).then((result) => {
      let shujv = result.data
      console.log(shujv)
      for (let list in shujv.result.display) {
        console.log(shujv.result.display[list]);
        this.product[0].values.push(shujv.result.display[list].productName);
      }
      this.preSelect.dkcp = [];
      this.preSelect.dkcp.push(this.product[0].values[0]);
      this.preSelect.productType.push(
        shujv.result.display[0].productType[0].name
      );
      this.preSelect.productSubType.push(
        shujv.result.display[0].productType[0].data.productSubType[0].name
      );
      for (let a in shujv.result.display[0].productType[0].data.productSubType[0]
        .data.productSeries) {
        this.product[1].values.push(
          shujv.result.display[0].productType[0].data.productSubType[0].data.productSeries[a].name
        );
      }
      this.preSelect.dkcp.push(this.product[1].values[0]);
      this.allData = shujv;
      console.log(this.product);
      console.log(this.allData);
    }).catch((err) => {
      console.log(err);
      Toast({message: err,duration: 3000});
    });
  }
};
</script>
<style>
#data{
  background-color:#f4f4f4;
  min-height:100vh;
}
#data .list{
  position: relative;
  height: 14vw;
}
#data .list p{
  position: absolute;
  margin: 0;
  line-height: 14vw;
  font-size: 4.5vw;
  left: 4vw;
  color: #6d6d6d;
}
#data .list span{
  position: absolute;
  line-height: 14vw;
  font-size: 4.5vw;
  right: 10vw;
  color: #171819;
}
#data .list img{
  position: absolute;
  width: 2.5vw;
  top: 4.4vw;
  right: 4vw;
}
.button{
  background-color: #ba6954;
  width: 86vw;
  line-height: 12vw;
  font-size: 4.5vw;
  margin: 10vw auto 0;
  text-align: center;
  color: #fff;
}
.select_back {
  position: fixed;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  top: 0;
  z-index: 2;
}
.select {
  position: fixed;
  width: 100vw;
  bottom: 0;
  background-color: #fff;
  z-index: 3;
}
.select .s_top {
  background-color: #f4f4f4;
  overflow: hidden;
  line-height: 10vw;
  font-size: 4vw;
}
.picker {
  /* position: fixed; */
  width: 100vw;
  /* bottom: 0; */
}
</style>



